* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Anton', sans-serif;
}

body {
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    background-image: url(./images/landscape-78058_1920.jpg);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}

div.main {
    height: 100vh;
    width: 100%;
}

h2 {
    height: 20vh;
    font-size: 6rem;
    text-transform: uppercase;
    text-align: center;
    padding-top: 20px;
    color: #ddd;
    text-shadow: -4px 4px 12px black;
    font-style: oblique;
    align-self: flex-start;
    letter-spacing: 1.7px;
}

.area {
    height: 65vh;
    width: 80%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    margin: 20px auto 10px;
    padding: 0 50px;
    background-color: rgba(1, 46, 78, 0.6);
    border-radius: 25px;
    color: #ddd;
}

.area h1 {
    padding-top: 20px;
    letter-spacing: 2px;
    font-size: 2.4rem;
    text-shadow: -2px 2px 6px black;
    text-align: center;
    color: #97c7f3;
    text-transform: uppercase;
}

.area p {
    width: 95%;
    font-size: 1.4rem;
    padding: 15px 30px 10px;
    text-align: center;
    line-height: 2.2rem;
    letter-spacing: 1.4px;
    text-shadow: -2px 2px 4px black;
    text-transform: uppercase;
    margin-bottom: -30px;
}

.area button, .formArea button {
    position: relative;
    width: 40%;
    height: 12%;
    border-radius: 15px;
    text-transform: uppercase;
    font-size: 1.5rem;
    color: #eee;
    background-image: linear-gradient(0deg, #154470, #67aff3);
    border: 2px solid #112940;
    box-shadow: 0 0 10px 0 #22527F;
    cursor: pointer;
    outline: none;
    transition: .5s;
    text-shadow: -2px 2px 7px black;
}

.area button:hover, .formArea button:hover {
    font-size: 1.8rem;
    box-shadow: 0 0 10px 0 white;
}

.area button.focus, .formArea button.focus, .outcome button.focus {
    border: 2px solid #ddd;
}

.area button.acr {
    width: 70%;
    height: 6%;
    font-size: 1rem;
    letter-spacing: 4px;
    color: #97c7f3;
    background-image: none;
    background-color: transparent;
    text-shadow: none;
    border: none;
    box-shadow: none;
    border-bottom: 3px solid #97c7f3;
    border-radius: 0;
    transition: 1s;
}

.area button.acr:hover {
    color: #ddd;
    border-bottom: 3px solid #ddd;
    letter-spacing: 6px;
}

.area input {
   height: 9%;
   min-height: 8%;
   width: 30%;
   margin: 10px 0;
   color: rgb(23, 75, 124);
   font-size: 1.2rem;
   letter-spacing: .5px;
   text-transform: uppercase;
   text-align: center;
   background-color: #ddd;
   border-width: 5px;
   border-style: ridge;
   border-color: #3e7eb9;
   box-shadow: 0px 0px 5px black;
   border-radius: 15px;
}

.area input[type=date]::-webkit-inner-spin-button {
    -webkit-appearance: none;
    display: none;
}

.modal_outer {
    position: absolute;
    top: 0%;
    left: 0%;
    width: 100%;
    height: 100vh;
    background-color: rgba(0,0,0,.7);
}

.modal_inner {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    height: 80vh;
    width: 80%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    background-color: rgba(255, 255, 255, 0.8);
    border-radius: 25px;
    color: #ddd;
}

.modal_inner p {
    color: #113a61;
    text-shadow: none;
    letter-spacing: 1.2px;
    font-size: 1.2rem;
}

.modal_inner button {
    height: 10%;
}

.formArea {
    height: 65vh;
    width: 70%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    margin: 20px auto 10px;
    background-color: rgba(1, 46, 78, 0.6);
    border-radius: 25px;
    color: #ddd;
}
  
.formArea p {
    font-size: 1.4rem;
    padding: 10px 20px 0;
    text-align: center;
    line-height: 1.8rem;
    letter-spacing: .8px;
    text-shadow: -2px 2px 4px black;
}

.formArea form {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    padding: 0 20px;
}

.formArea form input {
    width: 13%;
    margin-bottom: 20px;
    background-color: #ddd;
    text-align: center;
}

.formArea form input:first-of-type {
    background-color: #aaa;
}

.outcome {
    height: 65vh;
    width: 70%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
    margin: 20px auto 10px;
    background-color: rgba(61, 148, 229, 0.6);
    border-radius: 25px;
    color: #ddd;
}

.outcome p {
    font-size: 1rem;
    padding: 10px 20px 0;
    text-align: center;
    line-height: 1.5rem;
}

.outcome .color  {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 80%;
    width: 45%;
    background-color: rgba(255, 255, 255, .5);
    border-radius: 25px;
}


.outcome .color p {
    color: black;
    /* text-shadow: -2px 2px 4px black; */
    font-size: 2.5rem;
    padding: 10px;
    /* font-style: oblique; */
    letter-spacing: .9px;
}

.outcome .color .outcomeText {
    color: #ddd;
    font-size: .9rem;
    padding: 10px 10px 0;
    line-height: 1.5;
    text-align: center;
    letter-spacing: 1px;
}
.outcome .color.yellow .outcomeText, .outcome .color.green .outcomeText, .outcome .color.red .outcomeText {
    color: black;
    /* text-shadow: 1px 1px 2px white; */
}
.outcome .color.green .outcomeText {
    font-size: .9rem;
}

.outcome .color.red {
    background-color: rgba(255,0,0, .8);
}

.outcome .color.yellow {
    background-color: rgba(255,255,0, .8);
}

.outcome .color.green {
    background-color: rgba(0,255,0, .8);
}

.desc {
    height: 80%;
    width: 45%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    border-radius: 25px;
}

.top {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 65%;
    width: 100%;
    background-color: rgba(1, 46, 78, 0.6);
    border-radius: 25px;
}

.top p {
    padding: 0 10px;
    text-shadow: -1px 1px 2px black;
}
.top p span {
    color: red;
}

.top .motto {
    padding: 25px 0 0;
    text-transform: uppercase;
    font-size: 1.5rem;
    text-shadow: -2px 2px 6px black;
}

.bottom {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    height: 30%;
    width: 100%;
    background-color: rgba(1, 46, 78, 0.6);
    border-radius: 25px;
}

.bottom button {
    width: 45%;
    height: 60%;
    border-radius: 25px;
    text-transform: uppercase;
    font-size: 1.1rem;
    color: #ddd;
    background-image: linear-gradient(0deg, #154470, #67aff3);
    border: 2px solid #112940;
    box-shadow: 0 0 10px 0 #22527F;
    cursor: pointer;
    outline: none;
    transition: .3s;
    text-shadow: -2px 2px 5px black;
}

.bottom button:hover {
    font-size: 1.2rem;
    box-shadow: 0 0 10px 0 white;
}

footer {
    height: 10vh;
    width: 100%;
    display: flex;
    align-items: flex-end;
    align-self: flex-end;
}
  
footer a {
    width: 100%;
    height: auto;
    padding: 2px 0;
    color: #bbb;
    text-decoration: none;
    font-size: .8rem;
    font-style: italic;
    text-align: center;
    background-color: rgba(1, 46, 78, 0.8);
    text-transform: uppercase;
    letter-spacing: 5px;
}

